@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Oswald:wght@300;400;500;700&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --primary-font: 'Oswald', sans-serif;
    --logo-font: 'Dancing Script', cursive;
}

body {
    background-color: black;
    color: rgb(212, 212, 212);
    font-family: var(--primary-font);
    overflow-x: hidden;
}
.main {
    position: relative;
    width: 100vw;
    max-height: auto;
}

.header {
    position: sticky;
    top: 0;
    width: 100%;
    height: 6rem;
    display: flex;
    justify-content: center;
    z-index: 3;
    background-color: #000;
}
.header-inner {
    position: relative;
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo {
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: auto;
    height: 100%;
    font-size: 2.5rem;
    font-weight: bold;
    letter-spacing: 1.5px;
    font-family: var(--logo-font);
}
.logo h1 {
    line-height: 1;
}
.logo small {
    font-weight: 300;
    font-size: 0.7rem;
    line-height: 1;
    width: max-content;
    margin-left: auto;
}
.routes {
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}
.link {
    position: relative;
    padding: 8px 16px;
    letter-spacing: 1.3px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.2s ease-in;
}
.link a {
    text-decoration: none;
    color:rgb(212, 212, 212);
}
.link:hover {
    scale: 1.05;
}
.link::after {
    content: "";
    position: absolute;
    left: 0;
    top: 100%;
    width: 0;
    height: 3px;
    background-color: rgb(212, 212, 212);
    transform-origin: left;
    transition: all 0.3s linear;
}
.link:hover::after {
    width: 100%;
}
.cart {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(212, 212, 212);
    width: 40px;
    height: 40px;
    border-radius: 100%;
    cursor: pointer;
}
.cart .item-count {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 80%;
    bottom: 0;
    width: 20px;
    height: 20px;
    padding: 3px;
    background-color: #FFC54D;
    border-radius: 100%;
    font-size: 0.7rem;
    font-weight: bold;
    color: #000;
}
.landing {
    position: relative;
    width: 80%;
    height: calc(100vh - 7rem);
    display: grid;
    grid-template: 1fr / repeat(2, 1fr);
    margin: 0 auto;
}
.tag {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}
.tag p {
    color:rgb(150, 150, 150);
    font-size: 1.5rem;
    letter-spacing: 3px;
    margin: 0 0 8px;
}
.tag h3 {
    font-size: 2.5rem;
    width: 20ch;
    font-weight: bolder;
}
.tag button {
    border: none;
    font-size: 1.5rem;
    padding: 12px 36px;
    margin: 24px 0 0;
    border-radius: 32px;
    transition: all 0.2s linear;
    background-color: #F94C66;
    cursor: pointer;
}
.tag a {
    text-decoration: none;
    color: #fff;
}
.tag button:hover {
    letter-spacing: 1.3px;
}
.landing-img {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.landing-img img {
    max-height: 100%;
    max-width: 100%;
}


/*---------- SHOP ------------*/
.product-section {
    position: relative;
    width: 100%;
    height: auto;
    width: 80%;
    margin: 48px auto;
    display: grid;
    grid-template: repeat(1, 320px) / repeat(4, 1fr);
    grid-auto-rows: 320px;
    gap: 32px;
}

.product-section .card {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 16px;
    background-color: rgb(27, 27, 27);
    width: 100%;
    height: 100%;
    padding: 0 16px;
    transition: all 0.2s linear;
}
.product-section .card:hover {
    scale: 1.02;
}
.product-section a  {
    text-decoration: none;
    color:rgb(212, 212, 212);
}
.product-section .card .cover {
    position: relative;
    width: 100%;
    height: 70%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 0;
    border-bottom: 0.5px solid rgb(59, 59, 59);
}
.product-section .card img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
}
.product-section .description {
    position: relative;
    height: 22%;
    width: 100%;
}
.product-section .card h3 {
    font-size: 0.9rem;
}
.product-section .card h4 {
    position: absolute;
    bottom: 0;
    font-size: 0.9rem;
    font-weight: 300;
    margin: 12px 0 0;
    font-style: italic;
}

/*-------------- PRODUCTS ---------------*/
.product {
    position: relative;
    width: 60%;
    height: calc(100vh - 10em);
    margin: 32px auto 0;
    padding: 32px;
    display: grid;
    grid-template: repeat(1, 1fr) / repeat(2, 1fr);
    column-gap: 32px;
    background-color: rgb(27, 27, 27);
}
.product .product-img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product .product-img img {
    max-width: 100%;
    max-height: 100%;
}
.product .product-info {
    position: relative;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.product .product-info button {
    margin: 32px 0 0;
    border: none;
    background-color: #FFC54D;
    padding: 6px 12px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s linear;
}
.product .product-info button:hover {
    letter-spacing: 1.3px;
}

/*------ CART ---------*/

.cart-main {
    position: fixed;
    top: 0;
    left: 110%;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    color: #000;
    z-index: 5;
    overflow: hidden;
    transition: all 0.3s linear;
}
.cart-main.show {
    left: 0;
}
.cart-main .cart-section {
    height: 100%;
    width: 480px;
    margin-left: auto;
    padding: 32px 0;
    background-color: rgb(212, 212, 212);
}
.cart-section h1 {
    text-align: center;
    letter-spacing: 1.5px;
    font-size: 2rem;
    font-weight: bold;
    margin: 0 0 32px;
}
.cart-items {
    width: 80%;
    height: 60%;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: 1fr;
    row-gap: 16px;
    overflow-y: scroll;
    padding: 0 16px;
    margin: 0 auto;
}
.cart-items .item {
    display: grid;
    grid-template: repeat(1, 1fr) / 1fr 2fr;
    column-gap: 16px;
    width: 100%;
    height: 200px;
    padding: 32px;
    background-color: #afafaf;
}
.cart-items .item .item-img {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.cart-items .item .item-img img {
    max-width: 100%;
    max-height: 100%;
}
.change-items {
    width: 100%;
    height: auto;
    margin: 16px 0 0;
}
.change-items .adjust {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 8px 0 0;
    width: 8rem;
    height: 32px;
}
.change-items .adjust input {
    width: 48px;
    height: 32px;
    margin: 0 8px;
    text-align: center;
    padding: 4px;
    border: 1.5px solid #000;
    border-radius: 4px;
    outline: none;
    font-size: 1rem;
}

.change-items .adjust button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    font-weight: bold;
    font-size: 1.5rem;
    border: none;
    background-color: rgb(150, 150, 150);
    cursor: pointer;
}
.cart-actions {
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 16px 32px;
}
.cart-actions .total {
    margin-bottom: 12px;
}
.cart-actions button {
    width: 160px;
    height: 32px;
    font-size: 1rem;
    font-weight: bold;
    background-color: #53BF9D;
    border: none;
    cursor: pointer;
}
.cart-actions button.close {
    background-color: #F94C66;
    margin-top: 12px;
    color: #fff;
}